@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-search-title{
  margin-bottom: 32px;
  text-align: center;
}

.coin-search-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(-225deg, 
  #8a1fbb 0%,
  #7918f2 48%,
  #2b1664 100%);
  color: #e2e2e2;
  font-size: 17px;
}

.coin-search-input::placeholder{
  color: #e2e2e2;
}


