@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.coin-container{
    display: flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    justify-items: start;
    align-items: center;
    height: 80px;
    width: 900px;
    border-bottom: 1px solid #d7d7d7;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    width: 200px;
}

.coin-marketCap{
    width: 240px;
}

.coin-percent{
    width: 80px;
}

.red{
    color: red;
}

.green{
    color: green;
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-search-title{
  margin-bottom: 32px;
  text-align: center;
}

.coin-search-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(-225deg, 
  #8a1fbb 0%,
  #7918f2 48%,
  #2b1664 100%);
  color: #e2e2e2;
  font-size: 17px;
}

.coin-search-input::-webkit-input-placeholder{
  color: #e2e2e2;
}

.coin-search-input:-ms-input-placeholder{
  color: #e2e2e2;
}

.coin-search-input::placeholder{
  color: #e2e2e2;
}



